//默认导出
export default ({
/*  vuex中有四个方法,
其中大概用法为
在页面中将数据存储入vuex
this.$store.commit("name",box);
其中"name"为vuex.js中的方法名称  box为即将传入vuex中的数据值
然后再vuex中进行调取
name(state,res){
  console.log(res);
}
其中name为方法名  state是vuex中存储数据 res即为 上文的box数据值
然后再state中定义数值进行接收即可
console.log(this.$store.state.vuex,"页面获取");
其中this.$store是默认方法不可改动  state是在main.js中注册名称 vuexone是在main.js中state的模块名称

const store=new Vuex.Store({
  plugins: [createPersistedState()],
  modules:{
    vuexone
  }
});
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
});

  */
//  存储数据
  state:{
    login:"",
    //认证中心的data
    ApproveHomeData:[],
    //开户行中的编辑开票信息新增数据
    PlusTicketHome:{},
    //切换子父账号存贮新增的子账号名称
    TallageDataHome:[],
    //我要资讯中新增资讯
    messageHome:{},

    //找寻刷新页面是在哪一个主题组件
    ThemeIndexHome:"",
       //默认收获地址
    Daddress:{},  },
//  方法
  mutations:{
    //判断是登录还是没登录
    logins(state,res){
      state.login=res
    },
    HomeDatas(state,res){
      state.ApproveHomeData=res;
    },
    //开户行中的编辑开票信息新增数据
    PlusTicket(state,res){
      state.PlusTicketHome=res;
    },
    //我要资讯中新增资讯
    message(state,res){
      state.messageHome=res
      console.log(res);
    },
    TallageData(state,res){
      state.TallageDataHome=res;
    },
    //找寻刷新页面是在哪一个主题组件
    ThemeIndex(state,res){

      state.ThemeIndexHome=res;
    },
    Daddress(state,res){
      state.Daddress = res;
    },

  },
//  计算属性
  getters:{

  },
//  方法的异步操作
  action:{

  },
})



